header {
    padding: 0 25px;
    background-color: #72d4ffb3;
    border-bottom: 1px solid #cbd0d0;
    position: sticky;
    top: 0;
    width: 100%;
    height: 75px;
    transition: all 0.2s ease-in-out;
    z-index: 10;
    display: flex;

    .logo {
        align-self: center;
        color: #146b91;
        font-size: 1.3rem;
    }

    .navbar-nav {
        flex-direction: row;
        position: absolute;
        right: 30px;
        top: 12px;

        .nav-item {
            font-size: 35px;
            color: #146b91;
            margin-right: 20px;

            &:hover {
                color: #6b9cb1;
            }
        }
    }

    .menu-btn {
        border: none;
        cursor: pointer;
        display: none;
        background-color: transparent;
        font-size: 36px;
        color: #146b91;
        padding-top: 10px;
        outline: none;
    }

    .screen {
        position: fixed;
        top: 75px;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #00000061;
        visibility: hidden;
        opacity: 0;
        transition: opacity 0.3s;
    }

    @media (max-width: 660px) {
        .menu-btn {
            display: block;
            position: absolute;
            right: 50px;
            top: 0px;
        }

        .navbar-nav {
            display: flex;
            flex-direction: column;
            height: 100vh;
            width:20vh;
            position: fixed;
            text-align: center;
            top: 75px;
            right: 0;
            background: #f1f1f1eb;
            border-left: 2px solid rgba(223, 218, 218, 0.6);
            transform: translate(100%, 0);
            transition: transform 0.6s;

            a {
                height: 5rem;
                font-size: 18px;
                border-bottom: 1px solid #c8c8c8;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0;
            }
            .nav-item{
                margin-right: 0;
            }
        }

        &.opened-menu {
            nav {
                transform: translate(0, 0);
            }

            .screen {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}