.clean-list {
    padding: 10px;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    margin: 0;

    a {
        color: #085a75;
        text-decoration: none;
        min-width: 230px;
        align-self: center;
        border: 1px solid #085a75;
        border-radius: 25px;
        padding: 0.25rem;
        margin: 5px;
    }

    li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 15px 0 5px;
    }

    a:hover {
        text-decoration: underline;
    }
}